
.zAppPage-enter {
  max-width: 0px;
}
.zAppPage-enter-active {
  max-width: 996px;
  transition: max-width .5s;
}

.zAppPage-exit {
  max-width: 498;

  transition: all 300ms ease-out;

}
.zAppPage-exit-active {
  overflow:hidden;

  max-width: 0px;

}

.fadeBackground-enter {
  opacity: 0;
}
.fadeBackground-enter-active {
  opacity: .5;
  transition: opacity 500ms,opacity 1s;
}

.fadeBackground-exit {
  opacity: .5;

  transition: all 300ms ease-out;

}
.fadeBackground-exit-active {
  overflow:hidden;
  opacity: 0;

}

.parent {
  overflow: hidden; /* required */
  width: 100%; /* for demo only */
  height: 100% /* some non-zero number */;
  position: relative; /* required  for demo*/
}

.ribbon {
  margin: 0;
  padding: 0;
  background: red;
  color:white;
  padding:1em 0;
  position: absolute;
  top:0;
  right:0;
  -webkit-transform: translateX(30%) translateY(0%) rotate(45deg);
          transform: translateX(30%) translateY(0%) rotate(45deg);
  -webkit-transform-origin: top left;
          transform-origin: top left;
}
.ribbon:before,
.ribbon:after {
  content: '';
  position: absolute;
  top:0;
  margin: 0 -1px; /* tweak */
  width: 100%;
  height: 100%;
  background: rebeccapurple;
}
.ribbon:before {
  right:100%;
}

.ribbon:after {
  left:100%;
}

.calendar {
  width: 100%;
  color: black;
}


.shopTheToggle-enter {
  opacity: 0;
  max-height: 0px;
}
.shopTheToggle-enter-active {
  opacity: 1;
  max-height: 400px;
  transition: opacity 500ms,max-height 1s;
}

.shopTheToggle-exit {
  max-height: 200px;

  transition: all 300ms ease-out;

}
.shopTheToggle-exit-active {
  overflow:hidden;

  max-height: 0px;

}

